<template>
  <div class="main-wr-right-sub" id="textarea-element" ref="height">
    <div class="collections" v-for="photo in photos" :key="photo">
      <img class="photosoct fade-in" :src="photo" alt="" />
    </div>
    <div class="collections-link">
      <div class="date-link" @click="this.$router.push({ name: date1 })">
        {{ date1 }}
      </div>
      <div class="date-link" @click="this.$router.push({ name: date2 })">
        {{ date2 }}
      </div>
      <div class="date-link" @click="this.$router.push({ name: date3 })">
        {{ date3 }}
      </div>
      <div class="date-link" @click="this.$router.push({ name: date4 })">
        {{ date4 }}
      </div>
    </div>
  </div>
</template>

<script>
var fadeInElements = [];

import okt18_1 from "@/assets/okt18/okt18_1.jpg";
import okt18_2 from "@/assets/okt18/okt18_2.jpg";
import okt18_3 from "@/assets/okt18/okt18_3.jpg";
import okt18_4 from "@/assets/okt18/okt18_4.jpg";
import okt18_5 from "@/assets/okt18/okt18_5.jpg";
import okt18_6 from "@/assets/okt18/okt18_6.jpg";
import okt18_7 from "@/assets/okt18/okt18_7.jpg";
import okt18_8 from "@/assets/okt18/okt18_8.jpg";
import okt18_9 from "@/assets/okt18/okt18_9.jpg";
import { onMounted, onUnmounted } from "vue";
export default {
  setup() {
    onMounted(() => {
      const isElemVisible = (el) => {
        var rect = el.getBoundingClientRect();
        var elemTop = rect.top + 80; // 200 = buffer
        var elemBottom = rect.bottom;
        return elemTop < window.innerHeight && elemBottom >= 0;
      };

      const handleScroll = () => {
        for (var i = 0; i < fadeInElements.length; i++) {
          var elem = fadeInElements[i];
          if (isElemVisible(elem)) {
            elem.style.opacity = "1";
            fadeInElements.splice(i, 1); // only allow it to run once
          }
        }
      };
      fadeInElements = Array.from(document.getElementsByClassName("fade-in"));

      window.addEventListener("scroll", () => {});
      document.addEventListener("scroll", handleScroll);
      handleScroll();
    });

    onUnmounted(() => {});
  },
  name: "LandingGallery",
  mounted() {
    this.randomize(this.photos);
  },
  data() {
    return {
      photos: [
        okt18_1,
        okt18_2,
        okt18_3,
        okt18_4,
        okt18_5,
        okt18_6,
        okt18_7,
        okt18_8,
        okt18_9,
      ],
    };
  },
  methods: {
    setOpacity() {
      let scrolled = window.scrollY;
      console.log(scrolled);

      if (scrolled > 3000) {
        let links = document.getElementsByClassName("collections-links");
        links.style.opacity = 1;
      }
    },
    randomize(arr) {
      var i, j, tmp;
      for (i = arr.length - 1; i > 0; i--) {
        j = Math.floor(Math.random() * (i + 1));
        tmp = arr[i];
        arr[i] = arr[j];
        arr[j] = tmp;
      }
      return this.arr;
    },
  },
  props: ["date1", "date2", "date3", "date4"],
};
</script>

